<template>
	<div id="single-blog">
		<h1>{{blog.title}}</h1>
		<article>{{blog.content}}</article>
		<p>作者: {{blog.author}}</p>
		<p>分类:</p>
		<ul>
			<li v-for="category in blog.categories" :key="category">
				{{category}}
			</li>
		</ul>
		<button
			@click="deleteSingleBlog()"
			class="deleteBtn btn"
		>
			删除
		</button>
		<router-link
			class="editBtn btn"
			:to="'/edit/' + id"
		>
			编辑
		</router-link>
	</div>
</template>

<script>
import axios from '../axios-auth'
export default {
	name: "single-blog",
	data () {
		return {
			id: this.$route.params.id,
			blog: {}
		}
	},
	methods: {
		deleteSingleBlog () {
			axios.delete('/posts/' + this.id + '.json')
				.then(response => {
					this.$router.push({
						path: '/'
					})
				})
		}
	},
	created(){
		axios.get('/posts/' + this.id + '.json')
			.then((data) => {
				this.blog = data.data;
			})
	}
}
</script>
<style>
	#single-blog {
		max-width: 960px;
		margin: 0 auto;
		padding: 20px;
		background: #eee;
		border: 1px dotted #aaa;
	}

	.btn {
		display: inline-block;
		text-decoration: none;
		margin: 20px 5px;
		background: #555555;
		color: #fff;
		border: 0;
		padding: 10px;
		border-radius: 4px;
		font-size: 18px;
		cursor: pointer;
	}

	.deleteBtn {
		background: crimson;
	}

	.editBtn {
		background: #008CBA;
	}
</style>


